//店铺过店
<template>
  <div>
    <!-- 面包屑 -->
    <crumbs menu='店铺客流' subMenu='店铺过店' /> 
    <div class="container sql">
      <el-row >
        <el-col :span='2' class="fontCenter fontSet">区域选择:</el-col>
        <el-col :span='4' >
          <market @marketGet='marketGet' :marketSet='marketName'/>
        </el-col>
        <el-col :span='2' class="fontCenter fontSet borderSet">时间范围:</el-col>
        <el-col :span="10">
          <el-col :span='15'>
            <el-radio-group v-model="time" >
                        <el-radio-button label="近7日"></el-radio-button>
                        <el-radio-button label="近30日"></el-radio-button>
                        <el-radio-button label="近1年"></el-radio-button>
                        <el-radio-button label="自定义时间"></el-radio-button>
                      </el-radio-group>
          </el-col>
          <el-col :span='9'>
              <el-date-picker
                style="width:100%;"
                :disabled='datePickerAble'
                v-model="timeRange"
                type="daterange"
                :shortcuts="shortcuts"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                align="right">
              </el-date-picker>
          </el-col>
        </el-col>
        <el-col :span="6" >
            <el-button-group>
              <el-button type="primary" icon="el-icon-search" @click='onQuery' >查询</el-button>
              <el-button type="primary" icon="el-icon-refresh-right" @click="reSet">重置</el-button>
              <export-excel file-name="店铺过店" :header="['进店率排行','店铺名称','进店率','过店率排行','过店率','商场名称']"
              :filter-filed="['inRank','shopName','inShopRate','passRank','passShopRate','marketName']" :data="exportTable()">导出</export-excel>
            </el-button-group>
        </el-col>
      </el-row>
    </div>
    <el-card class="marginTop10" shadow="hover">
                  <el-row>
                      <el-table class="font-set"
                        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                        style="width: 100%;margin-bottom: 10px;margin-top:10px;font-size: 9px"
                        row-key="id"
                        border
                        :current-page="currentPage"              
                        stripe
                        @sort-change="sort_change"
                      >
                      <el-table-column
                        prop="inRank"
                        label="进店率排行"
                        sortable
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="shopName"
                        label="店铺名称"
                        sortable
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="inShopRate"
                        label='进店率'
                        :formatter="formatter"
                        sortable
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="passRank"
                        label="过店率排行"
                        sortable
                        align="center">
                      </el-table-column>
                      <el-table-column
                        prop="passShopRate"
                        label="过店率"
                        sortable
                        :formatter="formatter"
                        align="center">
                      </el-table-column>
                    </el-table>
            </el-row>
              <div class="pagination flexCenter" >
                  <el-pagination 
                      class="flexCenter"
                      @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :page-sizes="[10]"
                      :page-size="pagesize"
                      layout="total, sizes, prev, pager, next, jumper"
                      :total="total">     
                  </el-pagination>
              </div>
    </el-card>
  </div>
</template>
<script>
import Crumbs from '../pages/Crumbs'
import MarketSelect from '../pages/indexSelect/MarketSelect'
import axios from 'axios'
import ExportExcel from '../pages/excel/ExportExcel'

export default {
  data(){
    return {
      time:'近7日',
      timeRange:'',
      marketName:'万达',
      datePickerAble:true,
      shortcuts: [{
                text: '最近一周',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  return [this.$moment().subtract(7, 'days'), end]
                })()
              }, {
                text: '最近一月',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                  return [this.$moment().subtract(1, 'months'), end]
                })()
              }, {
                text: '最近一年',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setFullYear(start.getFullYear()-1)
                  // start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  return [this.$moment().subtract(1, 'years'), end]
                })()
      }],
      currentPage:1,//当前页
      pagesize:10,//每页数量
      total:0,//
      tableData:[],

    }
  },
  methods:{
    marketGet(val){
      this.marketName=val
    },
    handleSizeChange(val) {
            this.pagesize=val;
      },
    handleCurrentChange(val) {
            this.currentPage = val;
      },
    reSet(){
      this.marketName='万达'
      this.time='近7日'
      this.onQuery()
    },
    onQuery(){
      axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getShopByShop').then(res=>{
        let data=res.data.list.data
        data=data.sort((a,b)=>b.inShopRate-a.inShopRate)
          for(let i=0;i<data.length;i++){
            data[i].inRank=i+1
          }
          data=data.sort((a,b)=>b.passShopRate-a.passShopRate)
          for(let i=0;i<data.length;i++){
            data[i].passRank=i+1
          }
          data=data.sort((a,b)=>b.inShopRate-a.inShopRate)
        this.tableData=data
        this.total=this.tableData.length
        })
    },
    sort_change(params){  
        console.log(params)
        if(params.order=="ascending"){
          this.tableData.sort((a,b)=>a[params.prop]-b[params.prop])
        }else if(params.order=="descending"){
          this.tableData.sort((a,b)=>b[params.prop]-a[params.prop])
        }else {
          this.tableData.sort((a,b)=>a.inRank-b.inRank)
        }
        
      },
    formatter(...res){
      return [...res][2]+'%'
    },
    exportTable(){
      for(let i=0;i<this.tableData.length;i++){
        this.tableData[i].marketName=this.marketName
      }
      return this.tableData
    }
  },
  components:{
    crumbs:Crumbs,
    // areaSelect:AreaSelect,
    market:MarketSelect,
    ExportExcel
    // chart:Chart,
    // smallTag

    // indexSelect:IndexSelect,
  },
  mounted(){
    this.timeRange=[this.$moment().subtract(7, 'days'),this.$moment()]
    axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getShopByShop').then(res=>{
      let data=res.data.list.data
      data=data.sort((a,b)=>b.inShopRate-a.inShopRate)
        for(let i=0;i<data.length;i++){
          data[i].inRank=i+1
        }
        data=data.sort((a,b)=>b.passShopRate-a.passShopRate)
        for(let i=0;i<data.length;i++){
          data[i].passRank=i+1
        }
        data=data.sort((a,b)=>b.inShopRate-a.inShopRate)
      this.tableData=data
      this.total=this.tableData.length
      })
      this.textWidth=document.getElementById('text').offsetWidth
    
  },
  watch:{
    time(val){
      if(val==='近7日'){
        this.timeRange=[this.$moment().subtract(7, 'days'),this.$moment()]
      }
      if(val==="近30日"){
        this.timeRange=[this.$moment().subtract(1, 'months'),this.$moment()]
      }
      if(val==="近1年"){
        this.timeRange=[this.$moment().subtract(1, 'year'),this.$moment()]
      }
       if(val=='自定义时间'){
          this.datePickerAble=false
        }else this.datePickerAble=true
    },
  
  },
  computed:{
  }
}
</script>
<style scoped>
 .borderSet{
    /* border-right: 2px solid black; */
    border-left: 2px solid #C0C4CC;
  }
  .flexCenter{
    display: flex;
    align-items: center; /*垂直方向居中*/
    justify-content: space-around;  /*水平方向居中*/
    /* flex-wrap: wrap; */
  }
  .flexJus{
    display: flex;
    justify-content: space-around;  /*水平方向居中*/
  }
 .card-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
  }
  .card-header span{
    font-size: 20px;
    font-weight: bolder;
  }
  .fontSet{
     font-size: 18px;
    font-weight: bolder;
  }
  .flexCenter{
    display: flex;
    align-items: center; /* 垂直方向居中 */
    justify-content: flex-end;  /*水平方向居中*/

  }
.fontCenter{
    display: flex;
    align-items: center; /* 垂直方向居中 */
    justify-content: space-around;

  }
</style>